@charset "UTF-8";
/* montserrat-500 - cyrillic_latin */
@font-face {
  font-display: swap;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/montserrat-v26-cyrillic_latin-500.woff2") format("woff2"), url("../fonts/montserrat-v26-cyrillic_latin-500.ttf") format("truetype");
}
/* montserrat-600 - cyrillic_latin */
@font-face {
  font-display: swap;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/montserrat-v26-cyrillic_latin-600.woff2") format("woff2"), url("../fonts/montserrat-v26-cyrillic_latin-600.ttf") format("truetype");
}
/*===== global styles =====*/
* {
  box-sizing: border-box;
}

body {
  width: 100%;
  min-height: 100vh;
  font-family: "Montserrat", sans-serif;
  color: #1c1c20;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body.lock {
  overflow: hidden;
}

#luxy {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.container {
  width: 100%;
  max-width: 1586px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 43px;
}
@media (max-width: 1300px) {
  .container {
    max-width: 1088px;
    padding: 0 32px;
  }
}
@media (max-width: 991px) {
  .container {
    max-width: 832px;
    padding: 0 32px;
  }
}
@media (max-width: 767px) {
  .container {
    max-width: 100%;
    padding: 0 15px;
  }
}

a {
  color: inherit;
  text-decoration: none;
  outline: none;
}

html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul,
li {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  line-height: 1.2;
  vertical-align: baseline;
}

ol,
ul {
  list-style: none;
}

button,
input {
  padding: 0;
  font-family: inherit;
  font-size: inherit;
  border: none;
  appearance: none;
  outline: none;
  color: inherit;
  background-color: transparent;
}

button {
  cursor: pointer;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

.hidescrollbar {
  scrollbar-width: none;
}
.hidescrollbar::-webkit-scrollbar {
  -webkit-appearance: none;
  display: none;
  width: 0;
  height: 0;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  pointer-events: none;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-out, visibility 0.3s ease-out;
}

/*=== header_art6 styles===*/
.header_art6 {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  transition-delay: 0.3s;
}
@media (max-width: 991px) {
  .header_art6::after, .header_art6::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    z-index: 3;
    width: 100%;
    height: 100%;
    background-color: #fff;
    opacity: 0;
    pointer-events: none;
  }
  .header_art6::before {
    transition: opacity 0.2s ease-out;
    transition-delay: 0.3s;
  }
}

.header_art6__container {
  max-width: 1920px;
  min-width: 320px;
  min-height: 90px;
  margin: 0 auto;
  padding: 0 54px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0 50px;
}
@media (max-width: 1600px) {
  .header_art6__container {
    max-width: 1564px;
    min-height: 80px;
    padding: 0 32px;
  }
}
@media (max-width: 1200px) {
  .header_art6__container {
    padding: 0 42px 0 32px;
  }
}
@media (max-width: 767px) {
  .header_art6__container {
    min-height: 70px;
    padding: 0 18px 0 15px;
  }
}
@media (max-width: 575px) {
  .header_art6__container {
    gap: 0 10px;
  }
}
.header_art6__container::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -1px;
  z-index: 2;
  width: 100%;
  height: 1px;
  background-color: #f2f2f2;
}

.header_art6__wide-menu {
  width: 100%;
  max-width: 1403px;
}
@media (max-width: 1600px) {
  .header_art6__wide-menu {
    max-width: 1092px;
  }
}
@media (max-width: 1300px) {
  .header_art6__wide-menu {
    max-width: 944px;
  }
}

.header_art6__wrapper {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0 50px;
}
@media (max-width: 1200px) {
  .header_art6__wrapper {
    justify-content: flex-end;
  }
}
@media (max-width: 991px) {
  .header_art6__wrapper {
    justify-content: center;
  }
}
@media (max-width: 767px) {
  .header_art6__wrapper {
    display: none;
  }
}

.header_art6__logo {
  flex-shrink: 0;
  width: 133px;
}
.header_art6__logo img {
  display: block;
  width: 100%;
  height: auto;
}
@media (max-width: 991px) {
  .header_art6__logo {
    position: relative;
    z-index: 4;
  }
}

.header_art6__links {
  width: 100%;
  max-width: 793px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0 19px;
}
.header_art6__links > li {
  position: relative;
  padding: 28px 0;
}
.header_art6__links > li:nth-child(n+8) {
  display: none;
}
.header_art6__links a,
.header_art6__links button {
  display: inline-block;
  height: 100%;
  font-size: 16px;
  font-weight: 600;
  line-height: 2.25;
  letter-spacing: -0.03em;
  white-space: nowrap;
  color: #8e8e93;
  transition: color 0.2s ease-out;
}
@media (hover: hover) {
  .header_art6__links a:hover,
.header_art6__links button:hover {
    color: #1c1c20;
  }
}
@media (max-width: 1600px) {
  .header_art6__links {
    max-width: 686px;
  }
  .header_art6__links > li {
    padding: 23px 0;
  }
}
@media (max-width: 1300px) {
  .header_art6__links {
    max-width: 468px;
  }
  .header_art6__links > li:nth-child(n+3) {
    display: none;
  }
  .header_art6__links > li:last-child, .header_art6__links > li:nth-last-child(2), .header_art6__links > li:nth-last-child(3) {
    display: block;
  }
}
@media (max-width: 1200px) {
  .header_art6__links {
    max-width: 378px;
  }
  .header_art6__links > li:nth-child(6) {
    display: none;
  }
}
@media (max-width: 991px) {
  .header_art6__links {
    display: none;
  }
}

.header_art6__item-has-submenu ul {
  position: absolute;
  top: 100%;
  left: -40px;
  z-index: 4;
  width: 360px;
  padding: 22px 40px;
  background-color: #1c1c20;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 0.2s ease-out, visibility 0.2s ease-out;
}
.header_art6__item-has-submenu.active > ul {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}
.header_art6__item-has-submenu button {
  cursor: default;
}
.header_art6__item-has-submenu li:not(:last-child) {
  margin-bottom: 6px;
}
@media (hover: hover) {
  .header_art6__item-has-submenu a:hover {
    color: #fff;
  }
}

button.header_art6__more,
.header_art6__burger-btn {
  display: flex;
  align-items: center;
  gap: 0 8px;
  font-weight: 600;
  line-height: 2.25;
  letter-spacing: -0.03em;
  white-space: nowrap;
  color: #8e8e93;
  transition: color 0.2s ease-out;
}
@media (hover: hover) {
  button.header_art6__more:hover,
.header_art6__burger-btn:hover {
    color: #1c1c20;
  }
  button.header_art6__more:hover .header_art6__burger span, button.header_art6__more:hover .header_art6__burger::before, button.header_art6__more:hover .header_art6__burger::after,
.header_art6__burger-btn:hover .header_art6__burger span,
.header_art6__burger-btn:hover .header_art6__burger::before,
.header_art6__burger-btn:hover .header_art6__burger::after {
    background-color: #1c1c20;
  }
  button.header_art6__more:hover .header_art6__burger span,
.header_art6__burger-btn:hover .header_art6__burger span {
    transform: translateX(4px);
  }
  button.header_art6__more:hover .header_art6__burger::before,
.header_art6__burger-btn:hover .header_art6__burger::before {
    transform: translateX(9px);
  }
  button.header_art6__more:hover .header_art6__burger::after,
.header_art6__burger-btn:hover .header_art6__burger::after {
    transform: translateX(-2px);
  }
}

.header_art6__item-more ul {
  width: auto;
  min-width: 211px;
}
.header_art6__item-more ul ul {
  top: 0;
  left: unset;
  left: 100%;
  background-color: #8e8e93;
}
.header_art6__item-more ul ul a {
  color: #1c1c20;
}
@media (hover: hover) {
  .header_art6__item-more ul button:hover,
.header_art6__item-more ul a:hover {
    color: #fff;
  }
}
.header_art6__item-more ul li:nth-child(4) {
  display: none;
}
@media (max-width: 1200px) {
  .header_art6__item-more ul li:nth-child(4) {
    display: block;
  }
}
.header_art6__item-more .header_art6__item-has-submenu button {
  position: relative;
}
.header_art6__item-more .header_art6__item-has-submenu button::after {
  content: "";
  display: none;
  position: absolute;
  top: 0;
  left: 100%;
  height: 100%;
  width: 200px;
  background-color: transparent;
}
.header_art6__item-more .header_art6__item-has-submenu button:hover::after {
  display: block;
}

.header_art6__burger {
  display: block;
  position: relative;
  width: 27px;
  height: 28px;
}
.header_art6__burger span, .header_art6__burger::before, .header_art6__burger::after {
  position: absolute;
  left: 3px;
  height: 2px;
  background-color: #8e8e93;
  transition: background-color 0.2s ease-out, transform 0.4s ease-out;
}
.header_art6__burger span {
  top: 13px;
  width: 16px;
}
.header_art6__burger::before {
  content: "";
  top: 7px;
  width: 11px;
}
.header_art6__burger::after {
  content: "";
  bottom: 7px;
  width: 22px;
}

.header_art6__call {
  flex-shrink: 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 0 57px;
}
@media (max-width: 1600px) {
  .header_art6__call {
    gap: 0 31px;
  }
}
@media (max-width: 991px) {
  .header_art6__call {
    position: relative;
    z-index: 4;
  }
}

.header_art6__phone,
.header_art6__cta {
  font-weight: 600;
  line-height: 2.25;
  letter-spacing: -0.03em;
  transition: color 0.2s ease-out;
}
@media (hover: hover) {
  .header_art6__phone:hover,
.header_art6__cta:hover {
    color: #166ff5;
  }
}

.header_art6__phone,
.header_art6__moblie-phone {
  position: relative;
}
.header_art6__phone::after,
.header_art6__moblie-phone::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #166ff5;
}

.header_art6__burger-btn {
  display: none;
  position: relative;
  font-size: 18px;
}
.header_art6__burger-btn > span:first-child::before, .header_art6__burger-btn > span:first-child::after {
  position: absolute;
  top: -6px;
  transition: opacity 0.2s ease-out, visibility 0.2s ease-out;
}
.header_art6__burger-btn > span:first-child::before {
  content: "Меню";
  left: 23px;
}
.header_art6__burger-btn > span:first-child::after {
  content: "Закрыть";
  left: 0;
  opacity: 0;
  visibility: hidden;
}
@media (max-width: 991px) {
  .header_art6__burger-btn {
    position: relative;
    z-index: 4;
    min-width: 122px;
    display: flex;
    justify-content: flex-end;
    gap: 0 17px;
  }
}

.header_art6__mobile-menu {
  display: none;
  position: fixed;
  top: -100%;
  left: 0;
  z-index: -1;
  width: 100%;
  min-width: 320px;
  height: 100%;
  background-color: #fff;
  overflow-y: auto;
  transition: transform 0.3s ease-out;
}
@media (max-width: 991px) {
  .header_art6__mobile-menu {
    display: flex;
    justify-content: stretch;
    align-items: stretch;
    flex-direction: column;
  }
}

.header_art6__mobile-wrapper {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding-top: 81px;
}
@media (max-width: 767px) {
  .header_art6__mobile-wrapper {
    padding-top: 71px;
  }
}

.header_art6__mobile-links {
  flex-grow: 1;
  display: block;
  width: 100%;
  margin-bottom: 47px;
  padding: 0 32px;
}
.header_art6__mobile-links > li {
  padding: 0;
  transition: background-color 0.2s ease-out;
}
.header_art6__mobile-links > li a,
.header_art6__mobile-links > li button {
  display: inline-block;
  width: 100%;
  font-size: 20px;
  font-weight: 600;
  line-height: 2.3;
  text-align: left;
  color: #1c1c20;
}
.header_art6__mobile-links > li:not(:last-child) {
  margin-bottom: 11px;
}
@media (max-width: 767px) {
  .header_art6__mobile-links {
    padding: 13px 15px 0;
  }
}

.header_art6__mobile-item-has-submenu {
  position: relative;
  padding-right: 35px;
  overflow: hidden;
}
.header_art6__mobile-item-has-submenu::after {
  content: "";
  position: absolute;
  right: 0;
  top: 10px;
  width: 28px;
  height: 28px;
  background: url("./../images/header_art6/chevron-down.svg") center/contain no-repeat;
  transition: transform 0.2s ease-out;
}
.header_art6__mobile-item-has-submenu.submenu-opened::after {
  transform: scaleY(-1);
}
.header_art6__mobile-item-has-submenu > ul {
  max-height: 0;
  transition: max-height 0.2s ease-out;
}
.header_art6__mobile-item-has-submenu li a,
.header_art6__mobile-item-has-submenu button {
  font-weight: 500;
  color: #8e8e93;
}

.header_art6__mobile-call {
  min-width: 320px;
}

.header_art6__mobile-contacts {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 0 30px;
  margin-bottom: 12px;
  padding: 0 35px;
}
@media (max-width: 767px) {
  .header_art6__mobile-contacts {
    flex-wrap: wrap;
    gap: 5px 30px;
    padding: 0 15px;
  }
}

.header_art6__moblie-phone,
.header_art6__mobile-email {
  font-size: 20px;
  font-weight: 600;
  line-height: 2.3;
  letter-spacing: -0.03em;
  transition: color 0.2s ease-out;
}
@media (hover: hover) {
  .header_art6__moblie-phone:hover,
.header_art6__mobile-email:hover {
    color: #166ff5;
  }
}

.header_art6__moblie-phone::after {
  bottom: 7px;
}

.header_art6__mobile-cta {
  position: relative;
  width: 100%;
  min-height: 98px;
  padding: 13px 70px 0 35px;
  display: flex;
  font-size: 20px;
  font-weight: 600;
  line-height: 2.3;
  letter-spacing: -0.03em;
  color: #fff;
  background-color: #166ff5;
}
.header_art6__mobile-cta::after {
  content: "";
  position: absolute;
  right: 32px;
  top: 20px;
  width: 28px;
  height: 28px;
  background: url("./../images/header_art6/plus.svg") center/contain no-repeat;
}
@media (max-width: 767px) {
  .header_art6__mobile-cta {
    padding: 13px 50px 0 15px;
  }
  .header_art6__mobile-cta::after {
    right: 17px;
  }
}

.header_art6.mobile-menu-open {
  background-color: #fff;
}
@media (max-width: 991px) {
  .header_art6.mobile-menu-open::before, .header_art6.mobile-menu-open::after {
    opacity: 1;
  }
}
.header_art6.mobile-menu-open .header_art6__burger-btn {
  color: #1c1c20;
}
.header_art6.mobile-menu-open .header_art6__burger-btn > span:first-child::before {
  opacity: 0;
  visibility: hidden;
}
.header_art6.mobile-menu-open .header_art6__burger-btn > span:first-child::after {
  opacity: 1;
  visibility: visible;
}
.header_art6.mobile-menu-open .header_art6__burger-btn .header_art6__burger span, .header_art6.mobile-menu-open .header_art6__burger-btn .header_art6__burger::before, .header_art6.mobile-menu-open .header_art6__burger-btn .header_art6__burger::after {
  background-color: #1c1c20;
}
.header_art6.mobile-menu-open .header_art6__burger-btn .header_art6__burger span {
  transform: translateX(4px);
  opacity: 0;
}
.header_art6.mobile-menu-open .header_art6__burger-btn .header_art6__burger::before {
  transform-origin: center;
  width: 22px;
  transform: translateX(0) translateY(6px) rotate(45deg);
}
.header_art6.mobile-menu-open .header_art6__burger-btn .header_art6__burger::after {
  transform-origin: center;
  transform: translateX(0) translateY(-6px) rotate(-45deg);
}
.header_art6.mobile-menu-open .header_art6__mobile-menu {
  transform: translateY(100%);
}

.main {
  flex-grow: 1;
}

