.first_scr_main_art6 {
  margin-bottom: 120px;
  padding-top: 92px;
  background-size: 922px 663px;
}
.first_scr_main_art6 .container {
  position: relative;
  margin-top: 267px;
}
.first_scr_main_art6 .container::after {
  content: "";
  position: absolute;
  top: -203px;
  right: -220px;
  z-index: -1;
  width: 993px;
  height: 633px;
  background: url("./../images/first_scr_main_art6/first-bg.svg") center/contain no-repeat;
}
@media (max-width: 1600px) {
  .first_scr_main_art6 .container {
    margin-top: 198px;
  }
  .first_scr_main_art6 .container::after {
    top: -156px;
    right: -20px;
    width: 739px;
    height: 486px;
  }
